import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { Http } from "@angular/http";
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import { Iframe } from "../iframe/iframe";


@Component({
  selector: 'page-airone',
  templateUrl: 'airone.html',
})
export class Airone {
  iframe=Iframe;
find: any[];
images: any[];
tiltle:string;
  constructor(public navCtrl: NavController, public navParams: NavParams, 
    public httpGetReq : Http, public alertCtrl: AlertController) {
   console.log(this.navParams.data,'this.navParams');
   this.find=this.navParams.data;
   this.tiltle=this.navParams.data.name;
   
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Airone');
  }
gohome(){
    this.navCtrl.popToRoot();
  }


  showPrompt() {
    let prompt = this.alertCtrl.create({
      title: 'contact',
      message: "Enter a name for this new album you're so keen on adding",
      // inputs: [
      //   {
      //     name: 'title',
      //     placeholder: 'Title'
      //   },
      // ],
      buttons: [
        {
          text: 'Call',
          handler: data => {
            console.log('Cancel clicked');
          }
        }
        // {
        //   text: 'Save',
        //   handler: data => {
        //     console.log('Saved clicked');
        //   }
        // }
      ]
    });
    prompt.present();
  }
  

}




