import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Airport } from "../airport/airport";

import { Http } from "@angular/http";



@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  airport=Airport;
  pet="kittens";
  // searchQuery: string = '';
  // category: string[];
  

  slides = [];
  Category:any[];
  searchCate:any=[];
  constructor(public navCtrl: NavController, public httpGetReq : Http) {
    this.httpGetReq.get('data/data.json').subscribe((res) => {
      this.Category=res.json();
      this.initializecategory();
      
    });

    this.slides.push('../assets/image/newfour.jpg',
    '../assets/image/surfingtwo.jpg','../assets/image/surfingtwo.jpg',
    '../assets/image/surfingtwo.jpg');
    //  this.slides.push('../assets/image/surfingtwo.jpg');
    //   this.slides.push('../assets/image/surfingtwo.jpg');
    //    this.slides.push('../assets/image/surfingtwo.jpg');
    

  }
  
  
  initializecategory() {
    this.searchCate=this.Category;
    // console.log(this.searchCate,'this.searchCate');
  }

  getItems(ev: any) {
    // Reset items back to all of the items
    this.initializecategory();
    
    let val = ev.target.value;
  
    if (val && val.trim() != '') {
      this.searchCate = this.searchCate.filter((item) => {
        
        return (item.category.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }
}

  



